import React from "react";
import "./Header.css";
import './assests/css/header.scss'
import SearchIcon from "@material-ui/icons/Search";
import ShoppingBasketIcon from "@material-ui/icons/ShoppingBasket";
import { Link } from "react-router-dom";
import { useStateValue } from "./StateProvider";
import { auth } from "./firebase";

function Header() {
  const [{ basket, user }, dispatch] = useStateValue();

  const handleAuthenticaton = () => {
    if (user) {
      auth.signOut();
    }
  }

  return (
  <nav className="navbar navbar-expand-lg navbar-light tortinental-nav">
    
  <a class="navbar-brand" href="#">Navbar</a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="navbarTogglerDemo02">
    <ul class="navbar-nav mr-auto mt-2 mt-lg-0">
     
    </ul>
	<ul class="navbar-nav ml-auto">
		<li class="nav-item ">
			<Link to="/checkout">
				
					<ShoppingBasketIcon />
						<span className="header__optionLineTwo header__basketCount">
							{basket?.length}
						</span>
				
			</Link>
		</li>
		<li class="nav-item">
			
					<span className="header__optionLineTwo" >  </span>
			
		
		</li>
		<li class="nav-item">
			<Link to={!user && '/login'}>
				<div onClick={handleAuthenticaton} className="header__option">
					<span className="header__optionLineTwo" >  {!user ? 'Guest' : user.email}  {user ? 'Sign Out' : 'Sign In'} </span>
				</div>
			</Link>
		</li>
  </ul>
	
  </div>
</nav>
	

  );
}

export default Header;
